body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.type {
    color: #EB344A;
    border-color: black;
    font-family: 'Pacifico', cursive;
    font-size: 6em;
    text-align: center;
    height: 25vh;
    width: 100vw;
    -webkit-align-content: center;
            align-content: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.custom-select {
    font-family: 'Laila', serif;
}
.container {
margin-top: 10%;
}

.card {
    border-color: #EB344A;
    margin-bottom: 5%;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5%;
    -webkit-align-content: center;
            align-content: center;
    display: -webkit-flex;
    display: flex;
}

.card-header {
    background-color: #EB344A;
    color: aliceblue;
    font-family: 'Pacifico', cursive;
    font-size: 1.7em;
    text-align: center;
}

.card-title {
    font-family: 'Laila', serif;
    font-weight: bold;
    font-size: 1.4em;
}

.card-text {
    font-family: 'Laila', serif;
    font-size: 1.2em; 
}


